import Vue from 'vue'
import Router from 'vue-router'

import Index from "../pages/Index";
import Login from "../pages/Login"

import Materials from "../pages/Materials/Materials"
import PutStorage from "../pages/Materials/PutStorage";
import OutStorage from "../pages/Materials/OutStorage";
import ReturnStorage from "../pages/Materials/ReturnStorage";
import Check from "../pages/Materials/Check";
import Provider from "../pages/Materials/Provider";
import Project from "../pages/Materials/Project";
import Report from "../pages/Materials/Report";


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/login',
    },{
      path: '/login',
      name: 'Login',
      component: Login,
    },{
      path: '/index',
      name: 'Index',
      component: Index,
      children: [{
          path: '/materials',
          name: 'Materials',
          component: Materials
      },{
          path: '/putStorage',
          name: 'PutStorage',
          component: PutStorage
      },{
          path: '/outStorage',
          name: 'OutStorage',
          component: OutStorage
      },{
          path: '/returnStorage',
          name: 'ReturnStorage',
          component: ReturnStorage
      },{
          path: '/check',
          name: 'Check',
          component: Check
      },{
          path: '/project',
          name: 'Project',
          component: Project
      },{
          path: '/provider',
          name: 'Provider',
          component: Provider
      },{
          path: '/report',
          name: 'Report',
          component: Report
      },]
    },
  ]
})



