<template>
    <div class="app-container">
        <div class="grid-content">{{title}}</div>

        <!--查询-->
        <el-form :model="queryParams" ref="queryForm" :inline="true">
            <el-form-item label="物料名称" prop="matde">
                <el-input
                    v-model="queryParams.matde"
                    placeholder="请输入物料名称"
                    clearable
                    style="width: 240px"
                    @keyup.enter.native="handleQuery"
                />
            </el-form-item>
            <el-form-item label="盘盈/盘亏" prop="type">
                <el-select v-model="typeVal" multiple clearable placeholder="请选择" style="width: 240px">
                    <el-option
                        v-for="item in moveType"
                        :key="item.index"
                        :label="item.title"
                        :value="item.index">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="创建时间">
                <el-date-picker
                    v-model="dateRange"
                    size="small"
                    style="width: 240px"
                    value-format="yyyy-MM-dd"
                    type="daterange"
                    range-separator="-"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期"
                ></el-date-picker>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
                <el-button icon="el-icon-refresh" size="mini" @click="resetQuery">重置</el-button>
            </el-form-item>
        </el-form>

        <!--新增按钮-->
        <el-row :gutter="10" class="handle">
            <el-col :span="1.5">
                <el-button
                    type="primary"
                    icon="el-icon-plus"
                    size="mini"
                    @click="handleAdd"
                >新增</el-button>
            </el-col>
        </el-row>

        <!--表格-->
        <el-table
            :data="tableData"
            border
            :row-style="isStatus"
            v-loading="loading"
            style="width: 100%">
            <el-table-column type="index" width="50" />
            <!--<el-table-column prop="matnr" label="物料编码" />-->

            <el-table-column prop="matde" label="物料名称" />

            <el-table-column prop="number" label="数量" />

            <el-table-column prop="measuStr" label="单位" />

            <el-table-column prop="typeStr" label="盘盈/盘亏" />

            <el-table-column prop="remark" label="原因" />

            <el-table-column prop="StatusStr" label="状态" />

            <el-table-column prop="createTime" label="创建时间" width="150"/>

            <el-table-column prop="updateTime" label="修改时间" width="150"/>

            <el-table-column label="操作" align="center" fixed="right" class-name="small-padding fixed-width" width="150">
                <template slot-scope="scope">
                    <el-button v-if="scope.row.Status === 'Y'"
                               size="mini"
                               type="text"
                               icon="el-icon-edit"
                               @click="handleUpdate(scope.row)"
                    >修改</el-button>
                    <el-button v-if="scope.row.Status === 'Y'"
                               size="mini"
                               type="text"
                               icon="el-icon-right"
                               @click="handleBackout(scope.row)"
                    >撤销</el-button>
                    <el-button v-if="scope.row.Status === 'N'"
                               size="mini"
                               type="text"
                               icon="el-icon-back"
                               @click="handleBackout(scope.row)"
                    >取消撤销</el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--分页-->
        <el-pagination
            background
            layout="prev, pager, next"
            :current-page="queryParams.pageNum"
            :page-count="pageCount"
            @current-change="currentChange"
        >
        </el-pagination>

        <!-- 添加或修改对话框 -->
        <el-dialog :title="title" :visible.sync="open" width="1200px" append-to-body>
            <el-form ref="form" :model="form" :rules="rules" label-width="80px">
                <el-row>
                    <el-col :span="8">
                        <el-form-item label="盘盈/盘亏" prop="type" label-width="100px">
                            <el-select v-model="form.type" clearable placeholder="请选择">
                                <el-option
                                    v-for="item in moveType"
                                    :key="item.index"
                                    :label="item.title"
                                    :value="item.index">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>

                <el-row :gutter="10" class="handle handleAdd" v-if="isCreate">
                    <el-col :span="1.5">
                        <el-button
                            type="primary"
                            icon="el-icon-plus"
                            size="mini"
                            @click="handleAddItem"
                        >新增</el-button>
                    </el-col>
                </el-row>

                <el-row v-for="(item, index) in form.itemData" :key="index">
                    <el-col :span="6">
                        <el-form-item label="物料名称" :prop="`itemData[${index}].matnr`" :rules="rules.matnr">
                            <el-select v-model="item.matnr" filterable clearable :disabled="!isCreate" placeholder="请选择物料名称"
                                       @change="materialsChange(item, index)">
                                <el-option
                                    v-for="materials in materialsList"
                                    :key="materials.matnr"
                                    :label="materials.matde"
                                    :value="materials.matnr"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="4">
                        <el-form-item label="计量单位">
                            <el-select v-model="item.measu" disabled clearable placeholder="请输入单位">
                                <el-option
                                    v-for="unit in unitGroup"
                                    :key="unit.index"
                                    :label="unit.title"
                                    :value="unit.index">
                                    >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="4">
                        <el-form-item label="数量" :prop="`itemData[${index}].number`" :rules="rules.number">
                            <el-input-number v-model="item.number"
                                             controls-position="right"
                                             :min="0"
                                             :disabled="!isCreate"/>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="备注">
                            <el-input v-model="item.remark"  placeholder="请输入内容"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="1"  v-if="isCreate">
                        <i class="el-icon-delete" @click="itemDelete(index)"></i>
                    </el-col>
                </el-row>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="submitForm">确 定</el-button>
                <el-button @click="cancel">取 消</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import {invoicing} from "../../utils/invoicing";
    import {
        getInvoicingList,
        updateInvoicing,
        statusInvoicing,
        getMaterialsList,
    } from "../../utils/actions";
    import {addDateRange, addMoveType} from "../../utils/common";

    export default {
        data() {
            return {
                title: '盘点',
                loading: true,
                //总页数
                pageCount: 1,
                //数据
                tableData: [],
                //计量单位
                unitGroup: [],
                //移动类型
                moveType: [],
                //query时间数组
                dateRange: [],
                //query移动类型数组
                typeVal: [],
                // 查询参数
                queryParams: {},
                // 是否显示弹出层
                open: false,
                // 添加或修改参数
                form: {},
                //物料数组
                materialsList: [],
                //是否新增
                isCreate: true,
                rules: {}
            }
        },
        methods: {
            /** 查询列表 */
            getList() {
                this.loading = true;
                // console.log(this.queryParams);
                if(!this.queryParams.type) {
                    this.queryParams.type = "'D1','D2'";
                }
                getInvoicingList(this.queryParams).then(response => {
                    this.tableData = response.data;

                    //回显数据字典
                    this.selectDictLabel(this.tableData, 'measu', this.unitGroup);
                    this.selectDictLabel(this.tableData, 'type', this.moveType);
                    this.selectDictLabel(this.tableData, 'Status', invoicing.statusType);

                    this.pageCount = response.page_num;
                    this.loading = false;
                });
            },
            /** 搜索按钮操作 */
            handleQuery() {
                this.queryParams.pageNum = 1;
                addDateRange(this.queryParams, this.dateRange);
                addMoveType(this.queryParams, this.typeVal);
                this.getList();
            },
            /** 重置按钮操作 */
            resetQuery() {
                this.dateRange = [];
                this.typeVal = [];
                this.$refs.queryForm.resetFields();
                this.handleQuery();
            },
            /* 新增按钮*/
            handleAdd() {
                this.isCreate = true;
                this.open = true;
                this.form = invoicing.form();
            },

            /* 修改按钮*/
            handleUpdate(row) {
                this.isCreate = false;

                this.open = true;
                this.form = invoicing.form();
                invoicing.updateFrom(row, this.form);
            },

            /* 撤销按钮*/
            handleBackout(row) {
                const status = row.Status=="Y"?'N':'Y';
                statusInvoicing({id:row.id, status}).then(response => {
                    this.getList();
                });
            },

            /* 新增页面新增按钮*/
            handleAddItem() {
                this.form.itemData.push(invoicing.itemForm());
            },

            submitformHandel() {
                invoicing.submitFormToItemData(this.form);
                updateInvoicing({"putStorages":this.form.itemData}).then(response => {
                    this.open = false;
                    this.msgSuccess();
                    this.getList();
                });
            },

            submitForm() {
                this.$refs["form"].validate(valid => {
                    if (valid) {
                        this.submitformHandel();
                    }
                });
            },
            cancel() {
                this.open = false;
            },

            //选择物料修改时
            materialsChange(item, index) {
                const filter = this.materialsList.filter(materials => {
                    return materials.matnr === item.matnr;
                });
                if(filter) {
                    item.measu = filter[0].measu;
                }
            },

            itemDelete(index) {
                this.form.itemData.splice(index, 1);
            },

            currentChange(pageNum) {
                this.queryParams.pageNum = pageNum;
                this.getList();
            },

            //撤销行样式
            isStatus({row, column, rowIndex, columnIndex}) {
                if (row.Status === "N") {
                    return {
                        backgroundColor: '#f9bdbd'
                    }
                }
            }
        },
        created() {
            // console.log(invoicing);
            this.unitGroup = invoicing.unitGroup;
            this.moveType = invoicing.getType('D');
            this.queryParams = invoicing.queryParams();
            this.rules = invoicing.rules;

            this.getList();

            getMaterialsList().then(response => {
                this.materialsList = response.data;
            })
        }
    }
</script>

<style scoped lang="scss">
    .handle {
        margin-bottom: 20px;
    }
    .el-pagination {
        margin: 20px 0;
    }
    .el-icon-delete {
        margin: 4px 20px;
        font-size: 24px;
    }
    .handleAdd {
        margin-top: 30px;
    }

</style>



