<template>
    <div class="app-container">
        <div class="grid-content">{{title}}</div>

        <!--查询-->
        <el-form :model="queryParams" ref="queryForm" :inline="true">
            <el-form-item label="物料名称" prop="matde">
                <el-input
                    v-model="queryParams.matde"
                    placeholder="请输入物料名称"
                    clearable
                    style="width: 240px"
                    @keyup.enter.native="handleQuery"
                />
            </el-form-item>
            <el-form-item label="移动类型" prop="type">
                <el-select v-model="typeVal" multiple clearable placeholder="请选择" style="width: 240px">
                    <el-option
                        v-for="item in moveType"
                        :key="item.index"
                        :label="item.title"
                        :value="item.index">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="使用项目" prop="proId">
                <el-select v-model="queryParams.proId" filterable clearable placeholder="请选择" style="width: 240px">
                    <el-option
                        v-for="item in projectData"
                        :key="item.proId"
                        :label="item.name"
                        :value="item.proId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="创建时间">
                <el-date-picker
                    v-model="dateRange"
                    size="small"
                    style="width: 240px"
                    value-format="yyyy-MM-dd"
                    type="daterange"
                    range-separator="-"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期"
                ></el-date-picker>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
                <el-button icon="el-icon-refresh" size="mini" @click="resetQuery">重置</el-button>
            </el-form-item>
        </el-form>

        <!--新增按钮-->
        <el-row :gutter="10" class="handle">
            <el-col :span="1.5">
                <el-button
                    type="primary"
                    icon="el-icon-download"
                    size="mini"
                    @click="exportClick"
                >下载</el-button>
            </el-col>
        </el-row>

        <!--表格-->
        <el-table
            :data="tableData"
            border
            :row-style="isStatus"
            v-loading="loading"
            style="width: 100%">
            <el-table-column type="index" width="50" />
            <!--<el-table-column prop="matnr" label="物料编码" />-->

            <el-table-column prop="matde" label="物料名称" />

            <el-table-column prop="number" label="数量" />

            <el-table-column prop="measuStr" label="单位" />

            <el-table-column prop="po" label="单号" />

            <el-table-column prop="provider" label="供应商" />

            <el-table-column prop="typeStr" label="移动类型" />

            <el-table-column prop="proName" label="使用项目" />

            <el-table-column prop="StatusStr" label="状态" />

            <el-table-column prop="remark" label="备注" />

            <el-table-column prop="createTime" label="创建时间" width="150"/>

            <el-table-column prop="updateTime" label="修改时间" width="150"/>
        </el-table>

        <!--分页-->
        <el-pagination
            background
            layout="prev, pager, next"
            :current-page="queryParams.pageNum"
            :page-count="pageCount"
            @current-change="currentChange"
        >
        </el-pagination>

    </div>
</template>

<script>
    import {invoicing} from "../../utils/invoicing";
    import {export2Excel} from "../../utils/export2Excel";
    import {
        getInvoicingList,
        getProjectList,
        getMaterialsList,
        getProviderList,
    } from "../../utils/actions";
    import {addDateRange, addMoveType} from "../../utils/common";

    export default {
        data() {
            return {
                title: '物料报表',
                loading: true,
                //总页数
                pageCount: 1,
                //数据
                tableData: [],
                //计量单位
                unitGroup: [],
                //移动类型
                moveType: [],
                //query时间数组
                dateRange: [],
                //query移动类型数组
                typeVal: [],
                //项目数据
                projectData: [],
                // 查询参数
                queryParams: {},
                //物料数组
                materialsList: [],
                //供应商数组
                providerList: [],
            }
        },
        methods: {
            /** 查询列表 */
            getList() {
                this.loading = true;
                //console.log(this.queryParams);
                if(!this.queryParams.type) {
                    this.queryParams.type = "'A1','A2','A3','A4','A5','B1','B2','C1','C2','D1','D2'";
                }
                getInvoicingList(this.queryParams).then(response => {
                    this.tableData = response.data;

                    //回显数据字典
                    this.selectDictLabel(this.tableData, 'measu', this.unitGroup);
                    this.selectDictLabel(this.tableData, 'type', this.moveType);
                    this.selectDictLabel(this.tableData, 'Status', invoicing.statusType);

                    this.pageCount = response.page_num;
                    this.loading = false;
                });

            },
            /** 搜索按钮操作 */
            handleQuery() {
                this.queryParams.pageNum = 1;
                addDateRange(this.queryParams, this.dateRange);
                addMoveType(this.queryParams, this.typeVal);
                this.getList();
            },
            /** 重置按钮操作 */
            resetQuery() {
                this.dateRange = [];
                this.typeVal = [];
                this.$refs.queryForm.resetFields();
                this.handleQuery();
            },

            currentChange(pageNum) {
                this.queryParams.pageNum = pageNum;
                this.getList();
            },
            //撤销行样式
            isStatus({row, column, rowIndex, columnIndex}) {
                if (row.Status === "N") {
                    return {
                        backgroundColor: '#f9bdbd'
                    }
                }
            },

            //报表导出前的查询
            exportClick(){
                addDateRange(this.queryParams, this.dateRange);
                addMoveType(this.queryParams, this.typeVal);
                if(!this.queryParams.type) {
                    this.queryParams.type = "'A1','A2','A3','A4','A5','B1','B2','C1','C2','D1','D2'";
                }
                this.queryParams.pageNum = undefined;
                this.queryParams.pageSize = undefined;
                getInvoicingList(this.queryParams).then(response => {
                    const data = response.data;
                    //回显数据字典
                    this.selectDictLabel(data, 'measu', this.unitGroup);
                    this.selectDictLabel(data, 'type', this.moveType);
                    this.selectDictLabel(data, 'Status', invoicing.statusType);
                    //开始执行导出方法，此方法要放在括号里面，因为是异步请求数据，请求完才能执行导出
                    export2Excel(data, '物料报表'+ this.getFormatDate('MMDDhhmmss'));
                })

            },
        },
        created() {
            this.unitGroup = invoicing.unitGroup;
            this.moveType = invoicing.moveType;
            this.queryParams = invoicing.queryParams();

            this.getList();
            getProjectList().then(response => {
                this.projectData = response.data;
            });
            getMaterialsList().then(response => {
                this.materialsList = response.data;
            });
            getProviderList().then(response => {
                this.providerList = response.data;
            });
        }
    }
</script>

<style scoped lang="scss">
    .handle {
        margin-bottom: 20px;
    }
    .el-pagination {
        margin: 20px 0;
    }
    .el-icon-delete {
        margin: 4px 20px;
        font-size: 24px;
    }

</style>



