import {materialsGroup, unitGroup, moveType, statusType} from "../assets/data/group";
import el from "element-ui/src/locale/lang/el";

export const invoicing = {
    materialsGroup: materialsGroup,
    unitGroup: unitGroup,
    moveType: moveType,
    statusType: statusType,

    // 查询参数
    queryParams() {
        const queryParams = new Object();
        queryParams.pageNum = 1;
        queryParams.pageSize = 10;
        queryParams.matde = undefined;
        queryParams.type = undefined;
        queryParams.proId = undefined;
        queryParams.provider_id = undefined;
        queryParams.beginTime = undefined;
        queryParams.endTime = undefined;
        return queryParams;
    },

    rules: {
        type: [
            { required: true, message: "请选择移动类型", trigger: "blur" }
        ],
        matnr: [
            { required: true, message: "物料名称不能为空", trigger: "blur" }
        ],
        number: [
            { required: true, message: "数量不能为空", trigger: "blur" }
        ]
    },

    // 添加或修改参数
    form() {
        const form = new Object();
        form.po = undefined;
        form.providerId = undefined;
        form.type = undefined;
        form.proId = undefined;
        form.itemData = [this.itemForm()];
        return form;
    },

    itemForm() {
        const form = new Object();
        form.id = undefined;
        form.matnr = undefined;
        form.measu = undefined;
        form.number = undefined;
        form.remark = undefined;
        form.po = undefined;
        form.providerId = undefined;
        form.type = undefined;
        form.proId = undefined;
        form.Status = 'Y';
        form.amount = undefined;
        return form;
    },

    getType(flag) {
        const type = this.moveType.filter(item => {
            return item.index.indexOf(flag) != -1;
        })
        return type;
    },

    updateFrom(row, form) {
        form.itemData[0].id = row.id;
        form.itemData[0].matnr = row.matnr;
        form.itemData[0].measu = row.measu;
        form.itemData[0].number = row.number;
        form.itemData[0].remark = row.remark;
        form.itemData[0].Status = row.Status;
        form.po = row.po;
        form.providerId = row.providerId===0?undefined:row.providerId
        form.type = row.type;
        form.proId = row.proId===0?undefined:row.proId;
    },

    submitFormToItemData(form) {
        form.itemData.forEach(item => {
            item.po = form.po;
            if(form.providerId) {
                item.providerId = form.providerId
            }else {
                item.providerId = 0;
            }
            if(form.proId) {
                item.proId = form.proId
            }else {
                item.proId = 0;
            }
            item.type = form.type;
        })
    }
}



