<template>
    <div class="print-container">
        <div>
            <h1 class="print-title">取货单</h1>
            <div class="p-title2">
                <span class="p-left">领料单号：{{po}}</span>
            </div>
            <div class="p-title2">
                <span class="p-left">使用项目：{{project}}</span>
                <span class="p-right">日期：{{date}}</span>
            </div>
        </div>
        <table class="table" style="width: 100%;">
            <thead>
            <tr>
                <th rowspan="2">序号</th>
                <th rowspan="2">物料名称</th>
                <th rowspan="2">数量</th>
                <th rowspan="2">单位</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(item,index) in printData" :key="index">
                <td align="center" valign="middle">{{index+1}}</td>
                <td align="center" valign="middle">{{item.matde}}</td>
                <td align="center" valign="middle">{{item.number}}</td>
                <td align="center" valign="middle">{{item.measuStr}}</td>
            </tr>
            </tbody>
        </table>
        <div>
            <span class="print-sign">签名：<div class="underline"></div></span>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                project: '',
                date: '',
                po: ''
            }
        },
        props: {
            printData: Array
        },
        watch: {
            printData() {
                const data = this.printData[0];
                this.project = data.proName;
                this.po = data.po;
                this.date = data.createTime.split(" ")[0];
            }
        }
    }
</script>

<style scoped lang="scss">
    .print-container {
        width: 80%;
        color: black;
        position: relative;
        margin: 0 auto;
        .print-title {
            padding-top: 50px;
            font-size: 30px;
            text-align: center;
        }
        .p-title2 {
            position: relative;
            margin: 30px 0 10px;
            font-size: 16px;
            .p-right{
                position: absolute;
                right: 0;
            }
        }
        .print-sign {
            margin: 50px 0 0;
            position: absolute;
            right: 0;
            .underline {
                width: 150px;
                display: inline-block;
                border-bottom: 1px black solid;
                margin-bottom: -2px;
            }
        }
    }
    table,table tr th, table tr td { border:1px solid black; }

    th, td {
        padding: 10px;
    }



</style>



