<template>
    <el-container>
        <el-header height="50px">
            <img src="../../static/image/logo.png" class="logo-img">
            <span class="header-title">荣创物料管理系统</span>
            <img src="../../static/image/user.jpg" class="user-img">
        </el-header>
        <el-container>
            <el-aside width="15%">
                <Aside></Aside>
            </el-aside>
            <el-container>
                <el-main>
                    <!--<keep-alive>-->
                        <router-view></router-view>
                    <!--</keep-alive>-->
                </el-main>
            </el-container>
        </el-container>
    </el-container>
</template>

<script>
    import Aside from "../components/Aside";
    import Materials from "./Materials/Materials"
    export default {
        components: {
            Aside, Materials
        }
    }
</script>

<style lang="scss">
    html,body,#app{
        height: 100%;
    }
    .el-container {
        /*height: 100vh;*/
        height: calc(100vh - 50px);
        .grid-content {
            font-size: 12px;
            color: #606266;
            border-bottom: 1px solid rgb(220, 223, 230);
            margin-bottom: 20px;
            padding-bottom: 10px;
        }
    }
    .el-header {
        display: flex;
        align-items: center;
        background-color: #0082df;
        box-shadow: 0px 3px 5px #888888;
        .logo-img {
            width: 30px;
            height: 30px;
        }
        .user-img {
            width: 30px;
            height: 30px;
            position: absolute;
            right: 30px;
            border-radius: 5px;
        }
        .header-title {
            color: white;
            margin-left: 15px;
        }
    }
    .el-aside::-webkit-scrollbar {
        display:none;
    }
    .el-aside {
        box-shadow: 0px 3px 5px #888888;
    }



</style>



