<template>
    <div>
        <el-menu :default-active="active"
                 class="el-menu-vertical-demo"
                 :collapse="isCollapse"
                 background-color="#F2F2F2"
                 text-color="#606266"
                 active-text-color="#409Eff">
            <div v-for="item in menuData">
                <el-submenu v-if="item.children" :index="item.path">
                    <template slot="title">
                        <i :class="item.icon"></i>
                        <span>{{item.title}}</span>
                    </template>
                    <el-menu-item v-for="children in item.children"
                                  class="children-menu"
                                  @click="gotoMenu(children.path)"
                                  :index="children.path"
                                  :key="children.path">
                        <span slot="title">{{children.title}}</span>
                    </el-menu-item>
                </el-submenu>
                <el-menu-item v-else
                              @click="gotoMenu(item.path)"
                              :index="item.path"
                              :key="item.path">
                    <i :class="item.icon"></i>
                    <span slot="title">{{item.title}}</span>
                </el-menu-item>
            </div>

        </el-menu>
    </div>
</template>

<script>
    import {menu} from '../assets/data/menu'
    export default {
        data() {
            return {
                menuData: [],
                isCollapse: false,
                active:"11",
            };
        },
        methods: {
            gotoMenu(path){
                if(this.$route.path === path) {
                    return;
                }
                this.$router.push(path);
            }
        },
        created() {
            this.menuData = menu;
            //console.log(this.$route.path);
            this.active = this.$route.path;
        }
    }
</script>

<style scoped lang="scss">
    .el-menu-vertical-demo {
        /*height: 100vh;*/
        min-height: calc(100vh - 50px);
    }
    .children-menu {
        background-color: rgb(230, 230, 230) !important;
    }
</style>



