<template>
    <div class="app-container">
        <div class="grid-content">{{title}}</div>

        <el-form :model="queryParams" ref="queryForm" :inline="true">
            <el-form-item label="物料分组" prop="matgr">
                <el-select v-model="queryParams.matgr" clearable placeholder="请选择">
                    <el-option
                        v-for="item in materialsGroup"
                        :key="item.index"
                        :label="item.title"
                        :value="item.index">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="物料名称" prop="matde">
                <el-input
                    v-model="queryParams.matde"
                    placeholder="请输入物料名称"
                    clearable
                    @keyup.enter.native="handleQuery"
                />
            </el-form-item>
            <el-form-item>
                <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
                <el-button icon="el-icon-refresh" size="mini" @click="resetQuery">重置</el-button>
            </el-form-item>
        </el-form>

        <el-row :gutter="10" class="handle">
            <el-col :span="1.5">
                <el-button
                    type="primary"
                    icon="el-icon-plus"
                    size="mini"
                    @click="handleAdd"
                >新增</el-button>
            </el-col>
        </el-row>

        <el-table
            :data="tableData"
            border
            v-loading="loading"
            style="width: 100%">
            <el-table-column prop="matnr" label="物料编码" />

            <el-table-column prop="matde" label="物料名称" />

            <el-table-column prop="matgrStr" label="物料分组" />

            <el-table-column prop="measuStr" label="计量单位" />

            <el-table-column prop="amount" label="库存数量" />

            <el-table-column prop="remark" label="物料描述" />

            <el-table-column label="操作" align="center" class-name="small-padding fixed-width">
                <template slot-scope="scope">
                    <el-button
                        size="mini"
                        type="text"
                        icon="el-icon-edit"
                        @click="handleUpdate(scope.row)"
                    >修改</el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--分页-->
        <el-pagination
            background
            layout="prev, pager, next"
            :current-page="queryParams.pageNum"
            :page-count="pageCount"
            @current-change="currentChange"
        >
        </el-pagination>

        <!-- 添加或修改对话框 -->
        <el-dialog :title="title" :visible.sync="open" width="600px" append-to-body>
            <el-form ref="form" :model="form" :rules="rules" label-width="80px">
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="物料编码">
                            <el-input v-model="form.matnr" disabled clearable placeholder="物料编码" size="small"/>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="物料名称" prop="matde">
                            <el-input v-model="form.matde" clearable placeholder="请输入物料名称"/>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="物料分组" prop="matgr">
                            <el-select v-model="form.matgr" clearable placeholder="请选择">
                                <el-option
                                    v-for="item in materialsGroup"
                                    :key="item.index"
                                    :label="item.title"
                                    :value="item.index">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="计量单位" prop="measu">
                            <el-select v-model="form.measu" clearable placeholder="请选择">
                                <el-option
                                    v-for="item in unitGroup"
                                    :key="item.index"
                                    :label="item.title"
                                    :value="item.index">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="24">
                        <el-form-item label="物料描述">
                            <el-input v-model="form.remark" clearable placeholder="请输入物料描述"/>
                        </el-form-item>
                    </el-col>
                </el-row>


            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="submitForm">确 定</el-button>
                <el-button @click="cancel">取 消</el-button>
            </div>
        </el-dialog>


    </div>
</template>

<script>
    import {materialsGroup, unitGroup} from '../../assets/data/group';
    import {getMaterialsList, updateMaterials, updateProvider} from "../../utils/actions";

    export default {
        data() {
            return {
                title: '物料维护',
                loading: true,
                tableData: [],
                //总页数
                pageCount: 1,
                materialsGroup: [],
                unitGroup: [],
                // 查询参数
                queryParams: {
                    pageNum: 1,
                    pageSize: 10,
                    matde: undefined,
                    matgr: undefined
                },
                // 是否显示弹出层
                open: false,
                // 添加或修改参数
                form: {
                    matnr: undefined,
                    matde: undefined,
                    matgr: undefined,
                    measu: undefined,
                },
                rules: {
                    matde: [
                        { required: true, message: "物料名称不能为空", trigger: "blur" }
                    ],
                    matgr: [
                        { required: true, message: "请选择物料分组", trigger: "blur" }
                    ],
                    measu: [
                        { required: true, message: "请选择计量单位", trigger: "blur" }
                    ],
                }
            }
        },
        methods: {
            /** 查询列表 */
            getList() {
                this.loading = true;
                getMaterialsList(this.queryParams).then(response => {
                    this.tableData = response.data;
                    this.pageCount = response.page_num;

                    //回显数据字典
                    this.selectDictLabel(this.tableData, 'matgr', materialsGroup);
                    this.selectDictLabel(this.tableData, 'measu', unitGroup);
                    this.loading = false;
                })
            },
            /** 搜索按钮操作 */
            handleQuery() {
                if(this.queryParams.matde && !this.queryParams.matgr) {
                    this.msgError("请选择物料分组！");
                }
                this.queryParams.pageNum = 1;
                this.getList();
            },
            /** 重置按钮操作 */
            resetQuery() {
                this.$refs.queryForm.resetFields();
                this.handleQuery();
            },
            /* 新增按钮*/
            handleAdd() {
                this.open = true;
                this.resetForm('form');
            },

            /* 修改按钮*/
            handleUpdate(row) {
                this.open = true;
                this.resetForm('form');
                this.form.matnr = row.matnr;
                this.form.matde = row.matde;
                this.form.matgr = row.matgr;
                this.form.measu = row.measu;
                this.form.remark = row.remark;
            },

            submitForm() {
                this.$refs["form"].validate(valid => {
                    if (valid) {
                        updateMaterials(this.form).then(response => {
                            this.open = false;
                            this.msgSuccess();
                            this.getList();
                        });
                    }
                });
            },
            cancel() {
                this.open = false;
                this.resetForm('form');
            },
            // 表单重置
            resetForm() {
                this.form = {
                    matnr: undefined,
                    matde: undefined,
                    matgr: undefined,
                    measu: undefined,
                    remark: undefined,
                };
            },
            currentChange(pageNum) {
                this.queryParams.pageNum = pageNum;
                this.getList();
            }
        },
        created() {
            this.getList();
            this.materialsGroup = materialsGroup;
            this.unitGroup = unitGroup;
        }
    }
</script>

<style scoped lang="scss">
    .handle {
        margin-bottom: 20px;
    }
    .el-pagination {
        margin: 20px 0;
    }

</style>



