//入库、出库、退换货、盘点数据
export function getInvoicingList(params) {
    return request({
        url: 'invoicing/list',
        method: 'POST',
        params: params
    })
    //const req = new putArr;
}

export function updateInvoicing(params) {
    return request({
        url: 'invoicing/update',
        method: 'POST',
        data: params
    })
}

export function statusInvoicing(params) {
    return request({
        url: 'invoicing/status',
        method: 'POST',
        params: params
    })
}


