<template>
    <div class="app-container">
        <div class="grid-content">{{title}}</div>

        <!--查询-->
        <el-form :model="queryParams" ref="queryForm" :inline="true">
            <el-form-item label="项目名称" prop="name">
                <el-input
                    v-model="queryParams.name"
                    placeholder="请输入项目名称"
                    clearable
                    @keyup.enter.native="handleQuery"
                />
            </el-form-item>
            <el-form-item>
                <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
                <el-button icon="el-icon-refresh" size="mini" @click="resetQuery">重置</el-button>
            </el-form-item>
        </el-form>

        <!--新增按钮-->
        <el-row :gutter="10" class="handle">
            <el-col :span="1.5">
                <el-button
                    type="primary"
                    icon="el-icon-plus"
                    size="mini"
                    @click="handleAdd"
                >新增</el-button>
            </el-col>
        </el-row>

        <!--表格-->
        <el-table
            :data="tableData"
            border
            v-loading="loading"
            style="width: 100%">
            <el-table-column prop="proId" label="项目编码" />

            <el-table-column prop="name" label="项目名称" />

            <el-table-column prop="principal" label="项目负责人" />

            <el-table-column label="操作" align="center" class-name="small-padding fixed-width">
                <template slot-scope="scope">
                    <el-button
                        size="mini"
                        type="text"
                        icon="el-icon-edit"
                        @click="handleUpdate(scope.row)"
                    >修改</el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--分页-->
        <el-pagination
            background
            layout="prev, pager, next"
            :current-page="queryParams.pageNum"
            :page-count="pageCount"
            @current-change="currentChange"
        >
        </el-pagination>

        <!-- 添加或修改对话框 -->
        <el-dialog :title="title" :visible.sync="open" width="400px" append-to-body>
            <el-form ref="form" :model="form" label-width="100px" >
                <el-form-item label="项目编码" prop="proId" >
                    <el-input v-model="form.proId" disabled clearable placeholder="请输入项目编码"/>
                </el-form-item>
                <el-form-item label="项目名称" prop="name" :rules='{ required: true, message: "项目名称不能为空", trigger: "blur" }'>
                    <el-input v-model="form.name" clearable placeholder="请输入项目名称"/>
                </el-form-item>
                <el-form-item label="项目负责人" prop="principal" >
                    <el-input v-model="form.principal" clearable placeholder="请输入项目负责人"/>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="submitForm">确 定</el-button>
                <el-button @click="cancel">取 消</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import {getProjectList, updateProject, updateProvider} from "../../utils/actions";

    export default {
        data() {
            return {
                title: '供应商',
                loading: true,
                tableData: [],
                //总页数
                pageCount: 1,
                // 查询参数
                queryParams: {
                    pageNum: 1,
                    pageSize: 10,
                    name: undefined,
                },
                // 是否显示弹出层
                open: false,
                // 添加或修改参数
                form: {
                    proId: undefined,
                    name: undefined,
                    principal: undefined,
                },
                rules: {
                    matde: [
                        { required: true, message: "物料名称不能为空", trigger: "blur" }
                    ],
                    matgr: [
                        { required: true, message: "请选择物料分组", trigger: "blur" }
                    ],
                    measu: [
                        { required: true, message: "请选择计量单位", trigger: "blur" }
                    ],
                }
            }
        },
        methods: {
            /** 查询列表 */
            getList() {
                this.loading = true;
                getProjectList(this.queryParams).then(response => {
                    this.tableData = response.data;
                    this.pageCount = response.page_num;
                    this.loading = false;
                })

            },
            /** 搜索按钮操作 */
            handleQuery() {
                this.queryParams.pageNum = 1;
                this.getList();
            },
            /** 重置按钮操作 */
            resetQuery() {
                this.$refs.queryForm.resetFields();
                this.handleQuery();
            },
            /* 新增按钮*/
            handleAdd() {
                this.open = true;
                this.resetForm();
            },

            /* 修改按钮*/
            handleUpdate(row) {
                this.open = true;
                this.form.proId = row.proId;
                this.form.name = row.name;
                this.form.principal = row.principal;
            },

            submitForm() {
                console.log(this.form);
                this.$refs["form"].validate(valid => {
                    if (valid) {
                        updateProject(this.form).then(response => {
                            this.open = false;
                            this.msgSuccess();
                            this.getList();
                        });
                    }
                });
            },
            cancel() {
                this.open = false;
                this.resetForm('form');
            },
            // 表单重置
            resetForm() {
                this.form = {
                    proId: undefined,
                    name: undefined,
                    principal: undefined,
                };
            },
            currentChange(pageNum) {
                this.queryParams.pageNum = pageNum;
                this.getList();
            }
        },
        created() {
            this.getList();
        }
    }
</script>

<style scoped lang="scss">
    .handle {
        margin-bottom: 20px;
    }
    .el-pagination {
        margin: 20px 0;
    }

</style>



