// 回显数据字典
// 原数组， 字段, 字典值
export function selectDictLabel(tableData, field, group) {
    const object = groupObject(group);
    const fieldStr = field + "Str";
    if(tableData) {
        tableData.forEach(data => {
            let value = object[data[field]];
            Object.defineProperty(data, fieldStr, {value: value});
        })
    }
}

//数组对象转化为对象
function groupObject(group) {
    const object = {}
    group.forEach(item => {
        Object.defineProperty(object, item.index, {value: item.title});
    })
    return object;
};

// 添加日期范围
export function addDateRange(params, dateRange) {
    var search = params;
    search.beginTime = "";
    search.endTime = "";
    if (null != dateRange && '' != dateRange) {
        search.beginTime = dateRange[0];
        search.endTime = dateRange[1];
    }
    return search;
}

// 添加移动类型范围
export function addMoveType(params, type) {
    if (type) {
        let typeStr = "";
        for (let i = 0; i < type.length; i++) {
            typeStr +=  "'" + type[i] + "',";
        }
        typeStr = typeStr.slice(0, typeStr.length - 1);
        params.type = typeStr;
    } else {
        params.type = "";
    }
}

// 添加移动类型范围
export function getFormatDate(format="YYYY-MM-DD hh:mm:ss") {
    let date = new Date();
    let Y = date.getFullYear();
    let M = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1;
    let D = date.getDate();
    let h = date.getHours();
    let m = date.getMinutes();
    let s = date.getSeconds();
    const dateStr = format.replace("YYYY", Y)
        .replace("MM", M)
        .replace("DD", completeDate(D))
        .replace("hh", completeDate(h))
        .replace("mm", completeDate(m))
        .replace("ss", completeDate(s));
    return dateStr;
}

//补全0
function completeDate(value) {
    return value < 10 ? "0"+value:value;
}



